import { Component, OnInit } from '@angular/core';
import { EmployeeService } from '../services/employee.service';
import { MatTableDataSource, MatSort, MatPaginator, MatDialog, MatDialogConfig  } from '@angular/material';
import { EmployeeComponent } from '../employee.component';

@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {
  isPopupOpened: true;
  displayedColumns: string[] = ['EmployeeId', 'EmployeeFirstName', 'EmployeeLastName', 'EmployeeFullName','EmployeeFatherName','EmployeeDateOfBirth','EmployeeSex','EmployeeReligion','EmployeeMaritalStatus','EmployeeNationality','EmployeeHouse','EmployeeStreet','EmployeeCity','EmployeeState', 'EmployeePIN', 'EmployeePhoneNumber','EmployeeMobileNumber','EmployeeImage','EmployeeEmail','EmployeeDateOfJoin','EmployeeDateOfConfirm','EmployeeDepartment','EmployeeDesignation','EmployeeBasicSalary','EmployeeSalaryPerDay'];
  dataSource = new MatTableDataSource(this._employeeService._employeeDetail);
  employeeList = [];
  searchText;
  employeeFullList =  [];

  constructor(private dialog: MatDialog ,private _employeeService: EmployeeService) { }

  ngOnInit() {
    this.employeeList = this._employeeService.getEmployee();
    this.employeeFullList = this.employeeList;
  }
  // get employeeList() {
  //   return this._employeeService.getEmployee();
  // }

  search(){
  if (this.searchText !=""){
    this.employeeFullList = this.employeeFullList.filter(res =>{ 
      return res.EmployeeFullName.toLocaleLowerCase().match(this.searchText.toLocaleLowerCase())
     });
} else if (this.searchText ==""){
  this.ngOnInit();
  }
}


addEmployee() {
  const dialogConfig = new MatDialogConfig();
  this.isPopupOpened = true;
  const dialogRef = this.dialog.open(EmployeeComponent, {
    data: {}
  });


  dialogRef.afterClosed().subscribe(result => {
  });
  //dialogConfig.width = '60px';
 
}

editEmployee(id: number) {
  this.isPopupOpened = true;
  const tax = this._employeeService.getEmployee().find(c => c.EmployeeId === id);
  const dialogRef = this.dialog.open(EmployeeComponent, {
    data: {}
  });


  dialogRef.afterClosed().subscribe(result => {
   data: tax
  });
}

deleteEmployee(id: number) {
  this._employeeService.deleteEmployee(id);
}
}